@import "/src/styles/vars.scss";
@import "/src/styles/mixin.scss";

.main-button {
  @include main-button;
  @include main-button-text;
  color: "inherit";
  text-decoration: none;
}
.main-button:hover {
  color: $base-white;
}

.main-button__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 992px) {
  .main-button {
    max-width: 204px;
    max-height: 40px;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
  }

  .toCatolog-Button {
    padding-top: 12px;
    padding-bottom: 12px;
    display: inline-block;
    max-width: 294px;
    width: 100vw;
    cursor: pointer;
    @include main-button-text-1000px;
    background-color: $base-gold;
    text-align: center;
    text-transform: uppercase;
  }
}

@media (max-width: 768px) {
  .toCatolog-Button {
    padding-top: 10px;
    padding-bottom: 10px;
    display: inline-block;
    max-width: 224px;
    width: 100vw;
    cursor: pointer;
    @include main-button-text-790px;
    background-color: $base-gold;
    text-align: center;
    text-transform: uppercase;
  }

  .main-button {
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
  }
}
